html{
  scroll-behavior: smooth;
}

body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0E141B;
  color: #b9b9b9;
}

body {
   background: linear-gradient(90deg,#001820,#0E141B,#00070E);
     animation: gradient 5s ease infinite; 
      background-size: 400% 400%;
  
}
@-webkit-keyframes gradient{
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
     background-position: 0% 50%;
  }
}
@keyframes gradient{
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
     background-position: 0% 50%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.hero-content,h1{
  color: #256D85;
}

.bigName, .textHighlight{
  color: #00ADB5;
}

.textHighlight{
  line-height: normal;
  padding-top: 5px;
}

.skillbox
{
  background-color: rgba(37,109,133,0.14);
  border-radius: 8px;
  border-width: 0;
  color: #00ADB5;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  list-style: none;
  margin: 3px;
  padding: 7px 10px;
  text-align: center;
  transition: all 200ms;
  vertical-align: baseline;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.readmore{
  background-color:rgba(37,109,133,0.9);
  color: white;
  border: #00ADB5;
}

.expButtons{
  background-color: rgba(37,109,133,0.14);
  border-radius: 8px;
  border-width: 0;
  color: #b9b9b9;
  text-align: left;
  padding: 1% 2%;
}

#osintern{
  display: none;
}

hr.hr-1 {
  border: 0;
  height: 1.5px;
  background-image: linear-gradient(to right, rgba(37,109,133,0), rgba(0, 172, 181, 0.879), rgba(37,109,133,0));
  margin: 0;
  margin-top: 7%;
  margin-bottom: 7%;
}

.section-heading{
  padding: 0;
  padding-bottom: 3%;
}

