.nav{
  opacity: 0.9; 
}

#root{
  background: url("./assets/img/hero2-big.png") no-repeat center center;
	background-size: contain;
	background-position: top;
  opacity: 0.9;
}

#controlled-tab-example{
  color: #00ADB5;
}

.social-menu ul{
  position: fixed;
  top: 75%;
  left: 95%;
  padding: 0;
  margin: 0;
  transform: translate(-50%, -50%);

}

.social-menu ul li, .email a{
  list-style: none;
  margin: 0 15px;
}

.email{
  position: fixed;
  color: #00ADB5;
  text-decoration: none;
  transform: rotate(90deg);
}

.social-menu ul li .fab{
  font-size: 22px;
  line-height: 40px;
  transition: .3s;
  color: #fff;
}

.social-menu ul li .fab:hover{
  color: #fff;
}

.social-menu ul li a{
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(37,109,133,0.3);
  text-align: center;
  transition: .6s;
  box-shadow: 0 2px 4px #000000
}

.social-menu ul li a:hover{
  transform: translate(0, -10%);
}

.social-menu ul li:nth-child(1) a:hover{
  background-color: rgba(0, 0, 0, 0.829);
}
.social-menu ul li:nth-child(2) a:hover{
  background-color: #E4405F;
}
.social-menu ul li:nth-child(3) a:hover{
  background-color: #0077b5;
}
.social-menu ul li:nth-child(4) a:hover{
  background-color: #1D9BF0;
}

@media screen and (min-width: 992px) {
  #root{
    background: url("./assets/img/hero1-big.png") no-repeat center center;
    background-size: contain;
    background-position: top;
    opacity: 0.9;
  }
}

@media screen and (min-width: 769px) {
  #root{
    background: url("./assets/img/hero2-big.png") no-repeat center center;
    background-size: contain;
    background-position: top;
    opacity: 0.9;
  }
}


@media only screen and (max-width: 768px) {
  #root{
    background: url("./assets/img/hero-med.png") no-repeat center center;
    background-size: contain;
    background-position: top;
    opacity: 0.9;
  }
}

@media only screen and (max-width: 480px) {
  #root{
    background: url("./assets/img/hero1-big.png") no-repeat center center;
    background-size: cover;
    background-position: top;
    opacity: 0.9;
  }

  .about-section{
    flex-direction: column-reverse;
    justify-self: center;
  }

  .social-menu ul{
    top: 80%;
    left: 90%;
  }
}


#card1{
  background-image: url('./assets/img/gdg-iwd2023.png');
  background-repeat: no-repeat;
  background-size:inherit;
}
























.containers {
	position: relative;
	z-index: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	margin: 0 0 40px 0;
}

.containers .card {
  color: white;
	position: relative;
	width: 300px;
	height: 400px;
	background: rgba(255, 255, 255, 0.05);
	margin: 2%;
	box-shadow: 0 15px 35px rgba(0, 0, 0, 0.5);
	border-radius: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	backdrop-filter: blur(40px);
}

.containers .card .content {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	transition: 0.5s;
}

.containers .card:hover .content {
	transform: translateY(-110px);
}

.containers .card .content .eventTitle{
	position: relative;
	width: 80%;
	height: 100%;
	overflow: hidden;
}

