.containers .card .sci {
	position: absolute;
	bottom: 50px;
}

.containers .card .sci li {
	transform: translateY(50px);
  text-align: left;
	transition: 0.5s;
	opacity: 0;
}

.containers .card:hover .sci li {
	transform: translateY(0px);
	opacity: 1;
  font-size: 16px;
}


